
#loginForm {
  display: block;
  height: 100%;
}
#loginForm >div {
  width: 20%;
  height: 20%;
  box-sizing: border-box;
  border-right: solid 3px #000;
  float: left;
}
#mainLogin {
  width: 100%;
  height: 80%;
}
#mainLogin >div:first-child {
  display: block;
  height: 25%;
  width: 80%;
  border-right: solid 6px #000;
  border-bottom: solid 6px #000;
  box-sizing: border-box;
  padding: 1em;
}
#mainLogin >div:nth-child(2) {
  float: left;
  height: 75%;
  width: 20%;
  border-right: solid 3px #000;
  border-bottom: solid 6px #000;
  box-sizing: border-box;
}
#mainLogin >div:nth-child(3) {
  float: left;
  vertical-align: top;
  width: 60%;
  height: 75%;
  border-left: solid 3px #000;
  border-right: solid 6px #000;
  box-sizing: border-box;
  padding: 10%;
}
#mainLogin >div:nth-child(3) input {
  border: none;
}
.loginText {
  font-size: 36px;
  font-family: Futura;
}
#loginSubmit {
  float: left;
  width: 80%;
  height: 20%;
  display: block;
  border-left: solid 6px #000;
  border-top: solid 6px #000;
}


