
@import url("https://fonts.googleapis.com/css?family=Righteous");
* {
  margin: 0;
  padding: 0;
}
button {
  padding: 1em;
  border-style: none;
  font-weight: bold;
  font-size: 20px;
}
input {
  box-sizing: border-box;
  border: none;
  padding: 1em;
}
input:focus {
  outline-width: 0;
}
.redDiv {
  background-color: #d0021b;
  color: #fff;
}
.yellowDiv {
  background-color: #f8e71c;
  color: #000;
}
.blackDiv {
  background-color: #000;
  color: #fff;
}
.blueDiv {
  background-color: #4a90e2;
  color: #fff;
}
.whiteDiv {
  background-color: #fff;
  color: #000;
}
.title {
  font-weight: bold;
  font-family: Righteous, cursive;
  font-size: 36px;
}
.text {
  font-family: Futura;
  font-size: 16px;
  lineheight: 24px;
}
.nom-block {
  font-family: Futura;
  font-size: 20px;
  font-weight: bold;
}
.row {
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.row >div {
  height: 10vh;
  border: 3px solid #000;
}
.row >div.active {
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.row >div.appMiddle {
  overflow: scroll;
  height: 78.5vh;
}
.row >div:first-child {
  border-left-width: 0;
}
.row >div:last-child {
  border-right-width: 0;
}
.row:first-child .appMin,
.row:first-child .appMax {
  border-top-width: 0;
}
.row:last-child .appMin,
.row:last-child .appMax {
  border-bottom-width: 0;
}
.appMin {
  -ms-flex-preferred-size: 5%;
      flex-basis: 5%;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.appMax {
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.fade-enter-active,
.fade-leave-active {
  transition: transform 0.2s, opacity 0.3s;
  transition-time-function: ease-in-out;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateX(200px);
}
#imgToggle {
  position: absolute;
}
#Group:hover #barre1,
#Group:hover #barre2,
#Group:hover #barre3 {
  opacity: 0.9;
  transition: 0.2s;
}


